.log {
    max-width: 20rem;
    color: #3f3b00;
    list-style: none;
    margin: 2rem auto;
    padding: 0;
    text-align: center;
}

.log li {
    border-radius: 4px;
    animation: slide-in-from-left 1s cubic-bezier(0.075, 0.82, 0.165, 1) forwards;
    margin: 0.75rem;
}

@keyframes slide-in-from-left {
    0% {
        opacity: 0;
        transform: translateX(-30%);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.log li.highlighted {
    background-color: #3f3b00;
    color: white;
}