.h1 {
    font-family: "Baloo 2", sans-serif;
    font-size: 3rem;
}

@media (max-width: 1200px) {
    .h1 {
        font-size: 2.5rem;
    }
}

@media (max-width: 768px) {
    .h1 {
        font-size: 2rem;
    }
}

@media (max-width: 500px) {
    .h1 {
        font-size: 1.5rem;
    }
}