.dialog {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(91, 5, 140, 0.8);
    backdrop-filter: blur(10px);
    padding: 2rem;
    border: 1px solid #310233;
    border-radius: 50px;
    box-shadow: #F1EBBB 0px 0px 10px 5px;
    z-index: 1000;
    width: 55vw;
    padding-bottom: 4rem;
    margin: 2vh 1vw;
}

.score {
    font-size: 2rem;
    font-weight: 800;
    text-align: center;
    margin-bottom: 2rem;
    color: #EEEE;
    font-family: "Baloo 2", sans-serif;
}

@media (max-width: 768px) {
    .modal {
        width: 20rem;
        padding: 1.5rem;
    }
}

@media (max-width: 500px) {
    .modal {
        width: 15rem;
        padding: 1rem;
    }
}

.title {
    font-size: 3.5rem;
    font-weight: 800;
    text-align: center;
    margin-bottom: 1rem;
    color: #F1EBBB;
    font-family: "Baloo 2", sans-serif;
}

@media (max-width: 768px) {
    .h1 {
        font-size: 1.8rem;
    }
}

@media (max-width: 500px) {
    .h1 {
        font-size: 1.5rem;
    }
}

.button {
    background-color: #34093f;
    border: 4px solid #c80cbb;
    color: white;
    padding: 1rem;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 1rem;
    margin: 4px 1rem 1rem 1rem;
    cursor: pointer;
    border-radius: 25px;
    width: 10rem;
    transition: all 0.3s ease-in-out;
}

.button:hover {
    background-color: #1c0423;
    box-shadow: 0px 0px 10px 5px;
}

@media (max-width: 768px) {
    .button {
        width: 10rem;
        font-size: 0.9rem;
        padding: 0.8rem;
    }
}

@media (max-width: 500px) {
    .button {
        width: 10rem;
        font-size: 0.8rem;
        padding: 0.6rem;
    }
}

@media (max-width: 400px) {
    .button {
        width: 10rem;
        font-size: 0.8rem;
        padding: 0.6rem;
    }

    .title {
        font-size: 2.5rem;
    }

    .score {
        font-size: 1.5rem;
    }
}